.select2 {
  display: flex;
  align-items: center;
  width: 100% !important;
  border: 1px solid #E9E9E9;
  padding: 11.5px 15px;
  border-radius: 4px;
}

.select2 .selection {
  width: 100%;
}

.select2 .select2-selection__clear {
  display: none;
}

.select2.select2-container--default .select2-selection--single {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--c-text);
  font-size: 18px;
  border-radius: 0;
  width: 100%;
  border: none;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s;
}

.select2.select2-container--default .select2-selection--single .select2-selection__rendered,
.select2.select2-container--default .select2-selection--single .select2-selection__placeholder {
  width: 100%;
  padding: 0;
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow {
  position: relative;
  flex-shrink: 0;
  flex-grow: 0;
  height: 16px;
  width: 16px;
  display: block;
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow b {
  display: none;
}

.select2.select2-container--default .select2-selection--single .select2-selection__arrow::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  background: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.332 6.00004L7.9987 9.33337L4.66537 6.00004' stroke='%23141414' stroke-linecap='square'/%3E%3C/svg%3E") 0 0/100% 100% no-repeat;
  background-size: 100%;
  transition: all 0.3s;
  transform: rotate(0deg);
}

.select2.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow::before {
  content: "";
  transform: rotate(180deg);
  transition: all 0.3s;
}

.select2-container.select2-container--default.select2-container--open {
  z-index: 9999;
  border-radius: 4px 4px 0 0;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown {
  background-color: #fff;
  border: 1px solid #E9E9E9;
  border-radius: 0 0 4px 4px;
  top: -2px;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-search {
  display: none;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__options {
  max-height: 100%;
  padding-left: 0;
  padding-right: 0;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option {
  color: var(--c-text);
  font-size: 16px;
  line-height: 1.4;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  white-space: nowrap;
  width: 100%;
  background: transparent;
  padding-left: 15px;
  padding-right: 15px;
  cursor: pointer;
  transition: all 0.3s;
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option:hover {
  transition: all 0.3s;
  color: var(--c-accent);
}

.select2-container.select2-container--default.select2-container--open .select2-dropdown .select2-results__option.select2-results__option--highlighted {
  color: var(--c-accent);
  transition: all 0.3s;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: var(--c-text);
  transition: color 0.3s linear;
}

.field.active .select2 {
