.modal.hystmodal {
    .hystmodal__window {
        transition: all 0.3s linear;
        transform: scale(0);
        opacity: 0;
        width: auto;
        background-color: transparent;
    }
}

.modal.hystmodal--active {
    .hystmodal__window {
        margin: 0;
        transition: all 0.3s linear;
        transform: scale(1);
        opacity: 1;

        @include max(tablet) {
            margin: 0;
        }
    }
}

.hystmodal__shadow.hystmodal__shadow--show {
    background-color: $c-text;
    opacity: 0.3;
}

.modal {
    box-sizing: border-box;

    &__close {
        position: absolute;
        right: 24px;
        top: 24px;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: auto;
        min-width: auto;
        cursor: pointer;
        z-index: 20;
        color: $c-text-light;
        transition: color $tr-time;
        width: 24px;
        height: 24px;

        &:hover {
            color: $c-accent-hover;
        }

        svg {
            width: 24px;
            height: 24px;
        }

        @include max(mobile) {}
    }

    &__title {
        letter-spacing: -0.02em;
        text-align: center;
        padding: 0 40px;
        color: $c-text;
    }

    &__content {
        position: relative;
        background: $c-white;
        width: 100%;
        max-width: 540px;
        min-width: 540px;
        z-index: 9999;
        padding: 20px;
        border-radius: 4px;

        @include max(tablet) {
            width: calc(100% - 40px);
            margin: 0 auto;
        }

        @include max(mobile) {}
    }

    &__inner {
        margin-top: 24px;
    }
}